:root {
    --green: #105652;
    --red: #b91646;
    --yellow: #f9c041;
    --beige: #fbf3e4;
    --beige-darker: #dfd8ca;
    --text-dark: #0c413e;
    --text-light: #fbf3e4;

    --opacity: rgba(19, 10, 10, 0.5);
    --font-accent: "Bebas Neue", cursive;
    --font-primary: "Poppins", sans-serif;
}

.containerF {
    font-family: var(--font-primary);
    font-size: small;
    background-color: var(--beige);
    border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    position: relative;
    overflow: hidden;
    width: 700px;
    max-width: 100%;
    min-height: 480px;
    top: 90px;
    left: 20%;
}


h1 {
    font-weight: bold;
    margin: 0;
}

.form-container span {
    font-size: 12px;
}

.social {
    color: var(--green);
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
}

.auth {
    border-radius: 20px;
    border: #4e0d21 solid;
    background-color: var(--red);
    color: black;
    font-weight: bolder;
    padding: 10px 30px;
    width: 50%;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    transition: transform 80ms ease-in;
}

.auth:hover {
    opacity: 0.8;
}

.auth:active {
    transform: scale(0.95);
}

button.ghost {
    border-radius: 20px;
    border: black solid 1px;
    background-color: transparent;
    color: black;
    font-size: 15px;
    font-weight: bold;
    padding: 10px 30px;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
}

form {
    background-color: var(--beige);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    text-align: center;
}

input {
    background-color: #dfd8ca;
    border: none;
    border-radius: 10px;
    padding: 12px 15px 10px;
    margin: 5px 0;
    width: 100%;
}

.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}


.sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
}

.containerF.right-panel-active .sign-in-container {
    transform: translateX(100%);
}

.sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}

.containerF.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.6s;
}

@keyframes show {

    0%,
    49.99% {
        opacity: 0;
        z-index: 1;
    }

    50%,
    100% {
        opacity: 1;
        z-index: 5;
    }
}

.overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
}

.containerF.right-panel-active .overlay-container {
    transform: translateX(-100%);
}

.overlay {
    background: #edcc84;
    background: -webkit-linear-gradient(to right, #edcc84, #edcc84);
    background: linear-gradient(to right, #edcc84, #edcc84);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    color: black;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}

.containerF.right-panel-active .overlay {
    transform: translateX(50%);
}

.overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
}

.overlay-left {
    transform: translateX(-20%);
}

.containerF.right-panel-active .overlay-left {
    transform: translateX(0);
}

.overlay-right {
    right: 0;
    transform: translateX(0);
}

.containerF.right-panel-active .overlay-right {
    transform: translateX(20%);
}

.social-container {
    margin: 20px 0;
}

.social-container a {
    border: 1px solid #dddddd;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    height: 40px;
    width: 40px;
}

.social-container a:hover {
    color: var(--red);
    text-decoration: none;
    background-color: #1056525e;
}
